import React from 'react';
import ReactDOM from 'react-dom';
import {Jumbotron,Button} from 'react-bootstrap';
import {Link} from 'react-router-dom';
class Welcome extends React.Component{

  render(){
    return(
    <div>
      <section>
        <Jumbotron id="jumb1">
          <h1>Welcome !</h1>
          <p>My name is Abdelhadi Naimi. A Computer Science student, Web and Android developer.<br/>
          I am currently preparing my licence in computer science.</p>
          <p><Button bsStyle="primary" href="./about">Learn more</Button></p>
        </Jumbotron>
        <Jumbotron id="jumb2">
          <h1>Projects</h1>
          <p>All my projects are free and open source on my github page.</p>
          <p><Button bsStyle="primary" href="https://github.com/whizzmirray">Check out my github</Button></p>
        </Jumbotron>
        <Jumbotron id="jumb3">
          <h1>Gallery</h1>
          <p>I mostly work with <a href="https://processing.org/">Processing</a> and <a href="https://p5js.org/">p5.js</a>, A java and javascript based framework for grahical designers and visual art.</p>
          <p><Button bsStyle="primary" href="./gallery">Check out the gallery</Button></p>
        </Jumbotron>
      </section>

    </div>);
  }

}
export default Welcome;
