import React from 'react';
import ReactDOM from 'react-dom';
import {Navbar,NavItem,NavDropdown,MenuItem,Nav} from 'react-bootstrap';
import logo from '../img/logo.svg';
import { Route, Redirect,Link} from 'react-router-dom';
function flipCard(){
  return(
    <div className="flip-container">
    	<div className="flipper">
    		<div className="front">
    			<img src={logo} className="logo" height="35px"/>
    		</div>
    		<div className="back">
          <Link to='/' className="a-noDeco">Whizz-Mirray</Link>
    		</div>
    	</div>
    </div>
  );
}

//<a href='/' className="a-noDeco"></a>
function NavBar() {
  return (
    <Navbar collapseOnSelect id='nav-selector'>
      <Navbar.Header>
      <Navbar.Brand>
        {flipCard()}
      </Navbar.Brand>
      <Navbar.Toggle />
    </Navbar.Header>
     <Navbar.Collapse>
        <Nav>
          <li><Link to='/gallery'>Gallery</Link></li>
        </Nav>
        <Nav pullRight>
          <li className="about"><Link to='/about'>About</Link></li>
        </Nav>
      </Navbar.Collapse>
    </Navbar>);
}
export default NavBar;



